"use client";
import { Card, CardContent } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";

export default function AboutSection() {
  return (
    <section id="about" className="py-20 px-4 sm:px-6 lg:px-8 relative overflow-hidden">
      <div className="container mx-auto relative z-10">
        <h2 className="text-3xl md:text-4xl font-bold cyber-text mb-8 text-center animate-slide-in-up">
          About Me
        </h2>
        <div className="grid md:grid-cols-2 gap-8 max-w-5xl mx-auto">
          {/* Left side - Personal info */}
          <Card className="glass-card neon-glow animate-slide-in-left">
            <CardContent className="p-6">
              <p className="text-muted-foreground leading-relaxed mb-6">
                I'm a passionate security learner with a keen eye for building
                responsive, modern web interfaces. My journey in tech has been
                driven by curiosity, experimentation, and a constant desire to
                understand the intricacies of digital systems.
              </p>
              <div className="space-y-3">
                <div className="flex items-center justify-between">
                  <span className="font-semibold">Name:</span>
                  <span className="text-muted-foreground">Saif ul Islam</span>
                </div>
                <div className="flex items-center justify-between">
                  <span className="font-semibold">Age:</span>
                  <span className="text-muted-foreground">19</span>
                </div>
                <div className="flex items-center justify-between">
                  <span className="font-semibold">Location:</span>
                  <span className="text-muted-foreground">Pakistan</span>
                </div>
                <div className="flex items-center justify-between">
                  <span className="font-semibold">Email:</span>
                  <span className="text-muted-foreground">
                    saifulislam@example.com
                  </span>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Right side - Soft skills */}
          <Card className="glass-card neon-glow animate-slide-in-up">
            <CardContent className="p-6">
              <h3 className="text-xl font-semibold mb-4 cyber-text">Soft Skills</h3>
              <div className="flex flex-wrap gap-3">
                <Badge className="px-4 py-2 rounded-full bg-cyber-blue/20 text-cyber-blue border border-cyber-blue/50 neon-glow">
                  Communication
                </Badge>
                <Badge className="px-4 py-2 rounded-full bg-cyber-blue/20 text-cyber-blue border border-cyber-blue/50 neon-glow">
                  Problem Solving
                </Badge>
                <Badge className="px-4 py-2 rounded-full bg-cyber-blue/20 text-cyber-blue border border-cyber-blue/50 neon-glow">
                  Teamwork
                </Badge>
                <Badge className="px-4 py-2 rounded-full bg-cyber-blue/20 text-cyber-blue border border-cyber-blue/50 neon-glow">
                  Adaptability
                </Badge>
                <Badge className="px-4 py-2 rounded-full bg-cyber-blue/20 text-cyber-blue border border-cyber-blue/50 neon-glow">
                  Critical Thinking
                </Badge>
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </section>
  );
}
