"use client";
import { Card, CardContent } from "@/components/ui/card";
import { MessageSquare } from "lucide-react";

export default function LanguageSection() {
  const languages = [
    { name: "English", level: "Fluent" },
    { name: "Urdu", level: "Native" },
    { name: "Punjabi", level: "Conversational" },
  ];

  return (
    <section id="languages" className="py-20 px-4 sm:px-6 lg:px-8 relative overflow-hidden">
      <div className="container mx-auto relative z-10">
        <h2 className="text-3xl md:text-4xl font-bold cyber-text mb-12 text-center animate-slide-in-up">
          Languages
        </h2>
        <div className="grid md:grid-cols-3 gap-8 max-w-5xl mx-auto">
          {languages.map((lang, idx) => (
            <Card
              key={idx}
              className="glass-card neon-glow animate-slide-in-up text-center"
              style={{ animationDelay: `${idx * 0.2}s` }}
            >
              <CardContent className="p-6">
                <MessageSquare className="text-cyber-blue w-10 h-10 mx-auto mb-4" />
                <h3 className="text-xl font-semibold">{lang.name}</h3>
                <p className="text-muted-foreground">{lang.level}</p>
              </CardContent>
            </Card>
          ))}
        </div>
      </div>
    </section>
  );
}
