"use client";
import { Card, CardContent } from "@/components/ui/card";
import { Cpu } from "lucide-react";

export default function SkillsSection() {
  const skills = [
    { name: "HTML", level: 90 },
    { name: "CSS", level: 85 },
    { name: "JavaScript", level: 80 },
    { name: "C++", level: 70 },
    { name: "Python", level: 65 },
    { name: "Cybersecurity Basics", level: 60 },
  ];

  return (
    <section id="skills" className="py-20 px-4 sm:px-6 lg:px-8 relative overflow-hidden">
      <div className="container mx-auto relative z-10">
        <h2 className="text-3xl md:text-4xl font-bold cyber-text mb-12 text-center animate-slide-in-up">
          Skills
        </h2>
        <div className="grid md:grid-cols-2 gap-8 max-w-5xl mx-auto">
          {skills.map((skill, idx) => (
            <Card
              key={idx}
              className="glass-card neon-glow animate-slide-in-up"
              style={{ animationDelay: `${idx * 0.2}s` }}
            >
              <CardContent className="p-6">
                <div className="flex items-center justify-between mb-3">
                  <div className="flex items-center gap-2">
                    <Cpu className="text-cyber-blue w-5 h-5" />
                    <span className="font-semibold">{skill.name}</span>
                  </div>
                  <span className="text-muted-foreground">{skill.level}%</span>
                </div>
            
              </CardContent>
            </Card>
          ))}
        </div>
      </div>
    </section>
  );
}
