"use client";
import { Card, CardContent } from "@/components/ui/card";
import { Mail, Phone, MapPin } from "lucide-react";

export default function ContactSection() {
  const contacts = [
    {
      icon: <Mail className="text-cyber-blue w-6 h-6" />,
      title: "Email",
      value: "saifulislam@example.com",
      link: "mailto:saifulislam@example.com",
    },
    {
      icon: <Phone className="text-cyber-blue w-6 h-6" />,
      title: "Phone",
      value: "+92 300 1234567",
      link: "tel:+923001234567",
    },
    {
      icon: <MapPin className="text-cyber-blue w-6 h-6" />,
      title: "Location",
      value: "Pakistan",
      link: "https://goo.gl/maps/",
    },
  ];

  return (
    <section id="contact" className="py-20 px-4 sm:px-6 lg:px-8 relative overflow-hidden">
      <div className="container mx-auto relative z-10">
        <h2 className="text-3xl md:text-4xl font-bold cyber-text mb-12 text-center animate-slide-in-up">
          Contact Me
        </h2>
        <div className="grid md:grid-cols-3 gap-8 max-w-5xl mx-auto">
          {contacts.map((c, idx) => (
            <Card
              key={idx}
              className="glass-card neon-glow animate-slide-in-up text-center"
              style={{ animationDelay: `${idx * 0.2}s` }}
            >
              <CardContent className="p-6 flex flex-col items-center">
                {c.icon}
                <h3 className="text-xl font-semibold mt-4">{c.title}</h3>
                <a
                  href={c.link}
                  target="_blank"
                  rel="noopener noreferrer"
                  className="text-muted-foreground hover:text-cyber-blue transition-colors mt-2"
                >
                  {c.value}
                </a>
              </CardContent>
            </Card>
          ))}
        </div>
      </div>
    </section>
  );
}
